@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  background-color: #1a1a2e;
  background-image: url("./assets/images/sky.jpg");
  background-size: cover;
  background-position: center;
  font-family: "Inter", sans-serif;
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-basis: 10vh;
  flex-grow: 0;
  padding-top: 1rem;
  width: clamp(550px, 50vw, 650px);
}
main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}
.weather-display {
  color: white;
  display: grid;
  grid-template-columns: minmax(250px, 2fr) minmax(100px, 1fr);
  border-radius: 20px;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.4);
  background-color: rgba(0, 0, 0, 0.4);
  padding: 2rem;
  row-gap: 1rem;
  column-gap: 2rem;
  width: clamp(550px, 50vw, 650px);
  height: 450px;
  animation: dropIn 1.5s ease;
}
.name-container {
  height: 100%;
  width: 100%;
  font-size: 1rem;
  grid-column: span 2 / auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.name {
  font-size: 4rem;
}
.name-container .description {
  font-size: 1.1rem;
  opacity: 0.9;
  height: 100%;
}
.temperature {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: clamp(1rem, 3vw, 2rem);
  height: 100%;
  width: 100%;
}
.additional-temp-data {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}
.real-temperature {
  display: flex;
  font-size: clamp(4rem, 4.5vw, 5rem);
  line-height: 0.85;
  margin: 0;
  gap: 0.2rem;
  height: 100%;
  width: 100%;
}
.temperature-unit {
  flex-grow: 0;
  gap: 2rem;
}
.additional-info-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.5rem;
  height: 100%;
  width: 100%;
}
.feels-like,
.temp-max,
.temp-min {
  display: flex;
}
.real-temperature h2,
.feels-like p,
.temp-min p,
.temp-max p,
.wind-speed {
  font-variant-numeric: tabular-nums;
}
.searchElements {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.searchElements input {
  outline: none;
  border: none;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.4);
  height: 1.8rem;
  width: clamp(12rem, 5vw, 15rem);
  color: white;
  padding: 4px 8px;
}
button {
  padding: 0.5rem 1rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: transform 0.2s ease;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
button:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
footer {
  flex-basis: 5vh;
  background-color: rgba(0, 0, 0, 0.5);
  align-self: stretch;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
footer div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: clamp(550px, 50vw, 650px);
}
@keyframes dropIn {
  0% {
    transform: translateY(-20%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@media (max-width: 650px) {
  .weather-display {
    grid-template-columns: minmax(300px, 1fr);
    width: clamp(400px, 65vw, 550px);
    gap: 1rem;
    height: clamp(600px, 70vh, 800px);
  }
  .name-container {
    grid-column: 1;
  }
  .temperature,
  .additional-info-container {
    grid-column: 1;
  }
  .temperature {
    gap: 2rem;
  }
  header {
    width: clamp(400px, 65vw, 550px);
  }
  footer div {
    width: clamp(400px, 65vw, 550px);
  }
}
